<template>
    <div class="home">
      <el-container>
        <el-aside width="auto">
          <div class="logo">
            仿写后台
          </div>
           <el-menu
              class="el-menu-admin"
              :unique-opened="true"
              :router="true"
              :collapse="isCollapse"
              @open="handleOpen"
              @close="handleClose"
              background-color="#545c64"
              text-color="#fff"
              active-text-color="#ffd04b">
              <el-submenu index="1">
                <template slot="title">
                  <i class="el-icon-location"></i>
                  <span>用户管理</span>
                </template>
                  <el-menu-item index="/user">
                     <i class="el-icon-menu"></i>
                     <span slot="title">用户列表</span>
                  </el-menu-item>
              </el-submenu>

              <el-submenu index="2">
                <template slot="title">
                  <i class="el-icon-location"></i>
                  <span>权限管理</span>
                </template>
                  <el-menu-item index="/rights">
                     <i class="el-icon-menu"></i>
                     <span slot="title">权限列表</span>
                  </el-menu-item>
                  <el-menu-item index="/roles">
                      <i class="el-icon-menu"></i>
                     <span slot="title">角色列表</span>
                  </el-menu-item>
              </el-submenu>
            </el-menu>
        </el-aside>
        <el-container>
          <el-header>
            <i class="myicon myicon-menu toggle-btn" @click="toggle"></i>
            <div class="system-title">电商后台管理</div>
            <div>
              <span class="welcome">
                您好, {{$store.getters.username}}
              </span>
              <el-button type="text" @click="logout">退出</el-button>
            </div>
          </el-header>
          <el-main>
            <router-view></router-view>
          </el-main>
        </el-container>
      </el-container>
    </div>
</template>
           
<script>
import {getUserList} from '@/api'
export default {
  data () {
    return {
      isCollapse: false
    }
  },
   methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath)
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath)
      },
      toggle () {
        this.isCollapse = !this.isCollapse
      },
      logout () {
        localStorage.removeItem('mytoken')
        this.$router.push({name: 'Login'})
      }
    },
    mounted(){
      let params = {params: {query:'',pagenum: 1,pagesize: 1}}
      getUserList(params).then(res => {
        console.log(res)
      })
    }
}
</script>
<style lang="scss" scoped>
.home {
  height: 100%;
  background-color: #E5E5E5;
  .el-menu-admin:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
  .el-container {
    height: 100%;
  }
  .el-aside {
    background-color: #545c64;
  }
  .el-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #545c64;
  }
  .logo {
    height:60px;
    // background: url(../assets/logo.png);
    background-size: cover;
    background-color: #ccc;
    text-align: center;
    line-height: 60px;
  }
  .toggle-btn {
    padding: 0 10px;
    margin-left: -20px;
    font-size: 36px;
    line-height: 60px;
    color: #ccc;
    cursor: pointer;
    &:hover {
      background-color: #545c64;
    }
  }
  .system-title {
    font-size: 28px;
    color: white;
  }
  .welcome, {
    color: white;
  }
}
</style>


